@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar-container {
  height: 100px;
  width: 85%;
  margin: 0 auto;
  padding: 30px;
  background: transparent;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.logo {
  width: 70px;
}

.nav-links {
  margin-left: 30px;
}

.nav-link {
  text-decoration: none;
  margin-right: 25px;
  color: #555;
}

.nav-link:hover {
  color: #000;
}

.signup {
  display: flex;
}

.signin-btn {
  margin-right: 20px;
}

.register-btn {
  text-decoration: none;
}

.homescreen-section {
  text-decoration: none;
  height: 100vh;
  background: linear-gradient(
      289.71deg,
      #ffffff 3.77%,
      rgba(255, 255, 255, 0) 51.43%
    ),
    #edf4f7;
}

.homescreen-section .title {
  position: absolute;
  width: 929px;
  height: 205px;
  left: 8%;
  top: 308px;

  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 72px;
  line-height: 110%;
  display: flex;
  align-items: center;
  color: #000000;
  text-shadow: 2px 4px 5px #ffffff, -2px -4px 5px rgba(0, 0, 0, 0.25);
}

.homescreen-section .subtitle {
  position: absolute;
  width: 599px;
  height: 70px;
  left: 9%;
  top: 513px;

  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 160%;
  /* or 35px */

  display: flex;
  align-items: center;

  color: #000000;
}

.homescreen-section .therapy {
  position: absolute;
  width: 600px;
  height: 318.83px;
  right: 20%;
  top: 398px;
  background: url(/play/static/media/therapy.73fd0264.png);
}

.homescreen-section .book-btn {
  position: absolute;
  padding: 20px;
  height: 30px;
  vertical-align: baseline;
  text-align: center;
  text-decoration: none;
  left: 8%;
  font-size: 20px;
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 160%;
  top: 607px;
  background: #56c1e3;
  box-shadow: 3px 2px 7px 3px #ffffff, -2px -2px 7px 2px #cccccc;
  border-radius: 50px;
}

.homescreen-section .chat-now {
  background: #fff;
  color: #777;
  font-weight: normal;
  left: 20%;
}

.down-arrow {
  position: absolute;
  bottom: 0;
  left: 50%;
  background: url(/play/static/media/down.41ca0936.gif);
}
.neo-bottom-right {
  background: #56c1e3;
  font-weight: bolder;
  color: #ffffff;
  box-shadow: 2px 4px 5px 3px #ffffff, -2px -4px 5px 3px rgba(0, 0, 0, 0.25);
  border-radius: 50px;
  padding: 15px 25px;
}

.work-section {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: linear-gradient(
      55.43deg,
      #ffffff 12.17%,
      rgba(255, 255, 255, 0) 48.35%
    ),
    linear-gradient(
      250.79deg,
      #ffffff -14.9%,
      rgba(255, 255, 255, 0.745964) -9.01%,
      rgba(255, 255, 255, 0) 30.86%
    ),
    #edf4f7;
}

.work-container {
  display: flex;
  justify-content: center;
  align-content: center;
  text-align: center;
  margin-top: 50px;
}

.work-section .title {
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 160%;
  /* identical to box height, or 64px */
  text-align: center;
  color: #000000;
  /* Neo Bottom Left */
  text-shadow: -2px 4px 7px #ffffff, 2px -4px 7px rgba(0, 0, 0, 0.25);
}

.work-section .item {
  width: 313px;
  height: 358px;
  background: #e0f1f9;
  margin-right: 40px;
  margin-left: 40px;
  box-shadow: -2px 4px 7px 3px #ffffff, 2px -4px 7px 3px rgba(0, 0, 0, 0.25);
}

.work-section .item .image {
  margin-top: 30px;
}

.work-section .text {
  height: 50px;
  margin-top: 50px;
}

.work-section .text .title {
  font-family: Roboto;
  font-style: normal;
  font-weight: 900;
  font-size: 25px;
  line-height: 160%;
  /* identical to box height, or 40px */
  text-shadow: none;
  letter-spacing: 0.02em;
  color: #56c1e3;
}

.work-section .text .sub {
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 17px;
  width: 80%;
  margin: 0 auto;
  margin-top: 10px;
  line-height: 120%;
  /* or 24px */

  text-align: center;

  color: #000000;
}

